* {
  font-family: Helvetica, Arial sans-serif;
}

body {
  padding: 60px;
}

input {
  font-size: 1.2 rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid #ccc;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card small {
  padding: 5px;
  text-align: center;
  display: block;
}

.card img {
  max-height: 170px;
}